<script>
import qs from "qs";
import dayjs from "dayjs";

export default {
  data() {
    return {
      cData: {},
      pData: {},
      uData: {},
      list: {}, //表單內資料
      consultationItems: {},
    };
  },

  filters: {
    filterName(val) {
      if (val != undefined) {
        var name = val.split("(");
        return name[0];
      }
    },
    
  },

  mounted() {
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    this.pData = JSON.parse(sessionStorage.getItem("pData"));
    this.uData = JSON.parse(sessionStorage.getItem("uData"));
    // this.list = JSON.parse(sessionStorage.getItem("printData"));
    this.list = JSON.parse(this.$route.query.data);
    this.getConsultation(); //取得入住護理評估(本病歷的)
  },

  methods: {
    //回傳住民診斷
    diagnosis(){
      if (this.uData.diagnosis){
        const diagnosisArr = Object.values(this.uData.diagnosis).map(item=>{
          return item;
        });
        
        return diagnosisArr.reduce(function(prev, element) {
          if (prev == ''){
            return element;
          }else{
            return prev + ',' + element;
          }
        }, '');
      }
    },
    //取得轉介照會單(本病歷的)
    getConsultation() {
      var postData = {
        key: "u_snkey",
        value: this.uData.snkey
      };

      //this.$store.commit("setLoadingOn"); //讀取畫面on
      this.axios
        .post(
          "general/getByKey/" +
            this.$store.state.databaseName +
            "/consultation",
          qs.stringify(postData)
        )
        .then(rs => {
          if (rs.data.length) {
            this.consultationItems = rs.data[0];
          }
          // this.$store.commit("setLoadingOff"); //讀取畫面off
        });
    },
   
  }
};
</script>

<style lang="scss" scoped>
.ConsultationPrint {
  @import "node_modules/bootstrap/scss/bootstrap";
  @import "node_modules/bootstrap-vue/src/index.scss";

  .form_title {
    font-size: 1.2rem;
    // text-align: center;
    font-family: "微軟正黑體";
  }
  .vertical-mode {
    writing-mode: tb-rl;
    -webkit-writing-mode: vertical-rl;
    writing-mode: vertical-rl;
  }
}
</style>